 import './App.css';
 import {useState,useEffect} from 'react';
 import Header from './component/Header';
 import Recipes from './component/Recipes';
 import Footer from './component/Footer';
import Axios from 'axios';
  function App() {
   const[recipies,setRecipies]=useState("[]");

  const AppId="ee8218f6";
  const AppKey="aac233083a42d7668dfe08261ed16932";

  useEffect( ()=>{
  
    getRecipies();

  },[]);
 
const getRecipies = async()=>{
  const res=await Axios.get(`https://api.edamam.com/api/recipes/v2?type=public&app_id=ee8218f6&app_key=aac233083a42d7668dfe08261ed16932&diet=balanced`);
 
  setRecipies(res.data.hits);
}

 

  return (
    <div  >
      <header  >
      <Header   ></Header>
      <div className="container">
      <Recipes recipies={recipies}></Recipes>
      </div>
      </header>
      <Footer/>
    </div>
  );
}

export default App;
