import React from 'react'

const Recipes = props => {
    const {recipies}=props;
    return (
        <div className="row">
            {/* {console.log(recipies)}
             */}
            {  recipies.map(setrecipes =>(
                <div className="col-3 mt-5">
                    <div className="card">
                        <img src={setrecipes.recipe.image} className="img-fluid rounded-circle mx-auto p-3 w-60"/>
                        <div className="card-body">
                            <h5 className="text-center">{setrecipes.recipe.label}</h5>
                            <div>
                                <ul className="list-group list-group-flush">
                                    {
                                        setrecipes.recipe.ingredientLines.map(
                                            list =>
                                            (<li className="list-group-item">{list}</li>
                                            
                                        ))
                                     }
                                </ul>
                            </div>
                        </div>
                        </div>
                    </div>
            ))
        }
     
          
        </div>
    )
}

export default Recipes
